.toast {
    position: fixed;
    width:100%;
    z-index: 2000000;
    display: flex;
    flex-direction: column; }
.toast_box {
    position: relative;
    width: 200px;
    margin: 10px auto 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.65);
    border-radius: .1rem;
    color: #fff;
}
.toast_box:first-child{
    margin-top:50%;
}
.toast_text {
    bottom: 16%;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; }
.toast_icon {
    margin: 1% auto;
    width: 1rem;
    height: 1rem; }
.toast_loading {
    -webkit-animation: loading 1s steps(12, end) infinite;
    animation: loading 1s steps(12, end) infinite;
    background: url("data:image/png;base64,") no-repeat;
    background-size: 100%; }
.toast_success {
    background: url("data:image/png;base64,") no-repeat;
    background-size: 100%; }
.toast_error {
    background: url("data:image/png;base64,") no-repeat;
    background-size: 100%; }
.toast_info {
    background: url("data:image/png;base64,") no-repeat;
    background-size: 100%; }

@-webkit-keyframes loading {
    0% {
        -webkit-transform: rotate3d(0, 0, 1, 0deg);
        transform: rotate3d(0, 0, 1, 0deg); }
    100% {
        -webkit-transform: rotate3d(0, 0, 1, 360deg);
        transform: rotate3d(0, 0, 1, 360deg); } }

@keyframes loading {
    0% {
        -webkit-transform: rotate3d(0, 0, 1, 0deg);
        transform: rotate3d(0, 0, 1, 0deg); }
    100% {
        -webkit-transform: rotate3d(0, 0, 1, 360deg);
        transform: rotate3d(0, 0, 1, 360deg); } }
